.transcription-player {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.upload-section {
  margin-bottom: 20px;
}

.media-player {
  width: 100%;
  margin-bottom: 20px;
}

.media-player video,
.media-player audio {
  width: 100%;
}

.transcription-display {
  min-height: 100px;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.transcription-display p {
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

.loading {
  margin-top: 10px;
  color: #666;
}

.placeholder {
  color: #999;
  font-style: italic;
}

input[type="file"]:disabled {
  opacity: 0.6;
  cursor: not-allowed;
} 